.reviews {
	> div {
		padding: 15px 15px 5px 15px;
		background: #e5f4ff;
		border: 1px dotted #CCC;
		margin-bottom: 20px;
	}
	.author {
		font-weight: bold;
		position: relative;
		padding-left: 50px;
		.sub {
			font-size: 0.9rem;
			color: #8f8f8f;
			font-weight: normal;
		}
		&:before {
			content: '';
			position: absolute;
			left: 0;
			top: 0;
			width: 40px;
			height: 40px;
			//border-radius: 20px;
			overflow: hidden;
			//background-image: url('../img/driver-license.png');
			background-size: cover;
		}
	}
	.author-1:before {
		background-image: url('../img/driver-1.jpg');
	}
	.author-2:before {
		background-image: url('../img/driver-2.jpg');
	}
	.author-3:before {
		background-image: url('../img/driver-3.jpg');
	}
	.pro {
		font-style: italic;
	}
}

.need-text ol {
	margin: 10px 0;
	li {
		margin-bottom: 10px;
		line-height: 1.3em;
	}
}

.captcha {
	display: inline-block;
	margin-bottom: 10px;
	&:before, &:after {
		content: '';
		display: block;
		clear: both;
		margin: 0;
		padding: 0;
	}
}

.ico-block-5 span.green {
	display: inline;
	margin-top: 0;
	font-size: inherit;
	line-height: inherit;
	color: #1f940c;
	font-weight: 700;
}

.note {
	color: #ed2124;
	font-weight: bold;
	display: table;
	margin: 15px auto 20px auto;
	padding: 7px 15px;
	border: 2px solid #b70809;
	&:empty {
		color: #ed2124;
		font-weight: bold;
		display: none;
		margin: 0;
		padding: 0;
		border: none;
	}
	&:after {
		content: '';
		display: block;
		clear: both;
	}
}

.road + .container .row > div {
	&:nth-child(4) img, &:nth-child(5) img, &:nth-child(6) img {
		//display: none;
	}
	&:nth-child(4) .plus span, &:nth-child(5) .plus span, &:nth-child(6) .plus span {
		//height: 60px;
		display: block;
	}
	&:nth-child(4) .plus:before, &:nth-child(5) .plus:before, &:nth-child(6) .plus:before {
