.goog-inline-block {
  position: relative;
  display: -moz-inline-box;
  display: inline-block;
}

* {
  html .goog-inline-block, &:first-child + html .goog-inline-block {
    display: inline;
  }
}

.jfk-radiobutton {
  display: inline-block;
  outline: none;
  padding: 5px 7px;
  position: relative;
}

.jfk-radiobutton-radio {
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  border-radius: 50%;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  background: url(//ssl.gstatic.com/ui/v1/radiobutton/unchecked.png) -3px -3px;
  background: rgba(255, 255, 255, 0);
  border: 1px solid rgba(198, 198, 198, 1);
  height: 15px;
  left: 7px;
  margin: 0;
  outline: none;
  position: absolute;
  text-align: left;
  top: 6px;
  width: 15px;
}

.jfk-radiobutton {
  &:active .jfk-radiobutton-radio {
    background: rgba(235, 235, 235, 1);
    border-color: rgba(182, 182, 182, 1);
  }
  &:hover .jfk-radiobutton-radio {
    -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.1);
    -moz-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.1);
    box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.1);
    border-color: rgba(182, 182, 182, 1);
  }
  &:focus .jfk-radiobutton-radio {
    border-color: rgba(77, 144, 254, 1);
  }
}

.jfk-radiobutton-checked .jfk-radiobutton-radio {
  background: url(//ssl.gstatic.com/ui/v1/radiobutton/checked.png) -3px -3px;
  background: rgba(255, 255, 255, 0);
}

.jfk-radiobutton.jfk-radiobutton:focus .jfk-radiobutton-radio {
  background: url(//ssl.gstatic.com/ui/v1/radiobutton/unchecked_focused.png) -3px -3px;
  background: rgba(255, 255, 255, 0);
}

.jfk-radiobutton-checked {
  &.jfk-radiobutton:focus .jfk-radiobutton-radio {
    background: url(//ssl.gstatic.com/ui/v1/radiobutton/checked_focused.png) -3px -3px;
    background: rgba(255, 255, 255, 0);
  }
  .jfk-radiobutton-radio::after {
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    border-radius: 50%;
    background: rgba(96, 96, 96, 1);
    border: 2px solid #606060;
    box-sizing: border-box;
    content: '';
    display: block;
    height: 7px;
    left: 3px;
    position: relative;
    top: 3px;
    width: 7px;
  }
}

.jfk-radiobutton .jfk-radiobutton-label {
  cursor: default;
  margin-left: 22px;
}

.jfk-radiobutton-disabled {
  .jfk-radiobutton-radio {
    background: url(//ssl.gstatic.com/ui/v1/radiobutton/unchecked-disabled.png) -3px -3px;
    background: rgba(255, 255, 255, 0);
    border-color: rgba(241, 241, 241, 1);
  }
  &.jfk-radiobutton-checked .jfk-radiobutton-radio {
    background: url(//ssl.gstatic.com/ui/v1/radiobutton/checked-disabled.png) -3px -3px;
    background: rgba(255, 255, 255, 0);
    &::after {
      background: rgba(184, 184, 184, 1);
    }
  }
  .jfk-radiobutton-label {
    color: rgb(184, 184, 184);
  }
  &:active .jfk-radiobutton-radio, &:hover .jfk-radiobutton-radio {
    -webkit-box-shadow: none;
    -moz-box-shadow: none;
    box-shadow: none;
    background: rgba(255, 255, 255, 1);
    border-color: rgba(241, 241, 241, 1);
  }
}

.recaptcha-checkbox {
  border: none;
  font-size: 1px;
  height: 28px;
  margin: 4px;
  width: 28px;
  overflow: visible;
  outline: 0;
  vertical-align: text-bottom;
}

.recaptcha-checkbox-border {
  -webkit-border-radius: 2px;
  -moz-border-radius: 2px;
  border-radius: 2px;
  background-color: #fff;
  border: 2px solid #c1c1c1;
  font-size: 1px;
  height: 24px;
  position: absolute;
  width: 24px;
}

.recaptcha-checkbox-borderAnimation {
  background-image: url(data:image/png;base64,);
  background-repeat: no-repeat;
  border: none;
  height: 28px;
  outline: 0;
  position: absolute;
  width: 28px;
}

.recaptcha-checkbox-nodatauri.recaptcha-checkbox-borderAnimation {
  background-image: url(https://www.gstatic.com/recaptcha/api2/checkbox_sprite.png);
}

.recaptcha-checkbox-spinner-gif {
  -webkit-border-radius: 2px;
  -moz-border-radius: 2px;
  border-radius: 2px;
  background-color: #fff;
  background-size: 24px;
  border: 2px solid #c1c1c1;
  height: 24px;
  left: 0;
  position: absolute;
  top: 0;
  width: 24px;
}

.recaptcha-checkbox-spinner {
  background-image: url(data:image/png;base64,);
  background-repeat: no-repeat;
  border: none;
  height: 36px;
  left: -4px;
  outline: 0;
  position: absolute;
  top: -4px;
  width: 36px;
}

.recaptcha-checkbox-nodatauri.recaptcha-checkbox-spinner {
  background-image: url(https://www.gstatic.com/recaptcha/api2/loading_sprite.png);
}

.recaptcha-checkbox-spinnerAnimation {
  background-repeat: no-repeat;
  border: none;
  height: 38px;
  left: -5px;
  outline: 0;
  position: absolute;
  top: -5px;
  visibility: hidden;
  width: 38px;
}

.recaptcha-checkbox-checkmark {
  background-image: url(data:image/png;base64,);
  background-repeat: no-repeat;
  border: none;
  height: 30px;
  left: -5px;
  outline: 0;
  position: absolute;
  width: 38px;
}

.recaptcha-checkbox-nodatauri.recaptcha-checkbox-checkmark {
  background-image: url(https://www.gstatic.com/recaptcha/api2/checkmark_sprite.png);
}

.recaptcha-checkbox-hover {
  .recaptcha-checkbox-border, .recaptcha-checkbox-spinner-gif {
    -webkit-box-shadow: inset 0px 1px 1px rgba(0, 0, 0, 0.1);
    -moz-box-shadow: inset 0px 1px 1px rgba(0, 0, 0, 0.1);
    box-shadow: inset 0px 1px 1px rgba(0, 0, 0, 0.1);
    border: 2px solid #b2b2b2;
  }
}

.recaptcha-checkbox-focused {
  .recaptcha-checkbox-border, .recaptcha-checkbox-spinner-gif {
    border: 2px solid #4d90fe;
  }
}

.recaptcha-checkbox-active {
  .recaptcha-checkbox-border, .recaptcha-checkbox-spinner-gif {
    background-color: #ebebeb;
  }
}

.recaptcha-checkbox-disabled {
  .recaptcha-checkbox-border, .recaptcha-checkbox-spinner-gif {
    background-color: #f1f1f1;
  }
}

.recaptcha-checkbox-loading {
  .recaptcha-checkbox-spinnerAnimation {
    background-image: url(data:image/png;base64,);
    visibility: visible;
  }
  .recaptcha-checkbox-nodatauri.recaptcha-checkbox-spinnerAnimation {
    background-image: url(https://www.gstatic.com/recaptcha/api2/loading_bg_sprite.png);
  }
  .recaptcha-checkbox-spinner-gif {
    background-image: url('https://www.gstatic.com/recaptcha/api2/loading.gif');
  }
}

.recaptcha-checkbox-checked {
  .recaptcha-checkbox-border, .recaptcha-checkbox-spinner-gif {
    visibility: hidden;
  }
  .recaptcha-checkbox-checkmark {
    background-position: 0 -600px;
  }
}

.recaptcha-checkbox-expired {
  .recaptcha-checkbox-border, .recaptcha-checkbox-spinner-gif {
    border: 2px solid #ff0000;
  }
}

.recaptcha-checkbox-clearOutline.recaptcha-checkbox-focused {
  .recaptcha-checkbox-border, .recaptcha-checkbox-spinner-gif {
    border: 2px solid #c1c1c1;
  }
}

